@font-face {
  font-family: "SignPainter";
  font-style: normal;
  font-weight: 400;
  src: url("../../assets/fonts/SignPainter-Regular.ttf");
}

@font-face {
  font-family: "AmericanTypewriter";
  font-style: normal;
  font-weight: 400;
  src: url("../../assets//fonts/American-Typewriter-Regular.ttf");
}

.container {
  display: flex;
  justify-content: center;
  margin: 0;
}
.title {
  font-family: SignPainter;
  font-size: 200px;
  color: black;
  align-self: center;
  margin-block-end: 0.2em;
}

.clickHere {
  background: none;
  border: none;
  border: 1px solid black;
  border-radius: 50px;
  width: 500px;
  height: 100px;
  font-size: 32px;
  font-family: AmericanTypewriter;
  color: white;
}

.male {
  background: blue;
}

.female {
  background: pink;
}
